<template>
  <div
    align="center"
    style="margin-top: 20px; margin-left: 20px; margin-right: 20px"
  >
    <b-card id="schedule-card" style="max-width: 1000px">
      <b-row>
        <b-col>
          <div> (  ) .</div>
        </b-col>
      </b-row>
      <b-row>
        <b-col>
          <div>
            <b-form-radio-group
              v-model="selected"
              :options="options"
              class="mb-3"
              value-field="item"
              text-field="name"
            ></b-form-radio-group>
          </div>
        </b-col>
      </b-row>
    </b-card>
    <b-row>
      <b-col>
        <div
          id="green-colored-option-button"
          align="center"
          style="cursor: pointer"
          @click="applyMoimUpper()"
        >
           
        </div>
      </b-col>
    </b-row>
  </div>
</template>

<script>
import { LeaveMoim, applyMoimUpper } from "@/services/moim";
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      selected: {period: 30, money: 25000},
      options: [
        { item: {period: 30, money: 25000}, name: "30(25,000)" },
        { item: {period: 90, money: 70000}, name: "90(70,000)" },
        { item: {period: 180, money: 140000}, name: "180(140,000)" },
      ],
    };
  },
  methods: {
    async leavemoim() {
      const response = await LeaveMoim(
        this.MoimId,
        this.$cookies.get("MoimUserId")
      );
      if (response.status == 200) {
        this.$bvToast.toast(" .", {
          toaster: "b-toaster-top-right",
          appendToast: false,
          autoHideDelay: 3000,
        });
        setTimeout(() => this.$router.push("/mymoim"), 300);
      }
    },
    async applyMoimUpper() {
      console.log(this.selected);
      console.log(this.MoimId, this.selected.period, this.selected.money);
      await applyMoimUpper(this.MoimId, this.selected.period, this.selected.money);
      this.$emit("closeUpper");
      alert("    .");
    }
  },
  created() {},
  computed: {
    ...mapGetters("searchStore", { MoimId: "getSelectedMoimId" }),
  },
};
</script>

